import { Grid } from 'semantic-ui-react'
import { Link } from 'react-router-dom'


export default function NavbarHeader() {
    return (
        <Grid className='nav-wrapper'>
            <Grid.Column only='computer tablet' width={2} />
            <Grid.Column only='computer tablet' width={2} className="navbar-button-wrapper">
                <Link className='navbar-button' 
                    to={'/home'}>
                    Home
                </Link>
            </Grid.Column>
            <Grid.Column only='computer tablet' width={2} className="navbar-button-wrapper">
                <Link className='navbar-button' 
                    to={'/home'}>
                    Experience
                </Link>
            </Grid.Column>
            <Grid.Column only='computer tablet' width={2} className="navbar-button-wrapper">
                <Link className='navbar-button' 
                    to={'/home'}>
                    Skills
                </Link>
            </Grid.Column>
            <Grid.Column only='computer tablet' width={2} className="navbar-button-wrapper">
                <Link className='navbar-button' 
                    to={'/home'}>
                    Education
                </Link>
            </Grid.Column>
            <Grid.Column only='computer tablet' width={2} className="navbar-button-wrapper">
                <Link className='navbar-button' 
                    to={'/home'}>
                    Projects
                </Link>
            </Grid.Column>
            <Grid.Column only='computer tablet' width={2} className="navbar-button-wrapper">
                <Link className='navbar-button' 
                    to={'/home'}>
                    Awards
                </Link>
            </Grid.Column>
            <Grid.Column only='computer tablet' width={2} />
        </Grid>
    )
}