import { Component } from 'react'
import { Grid } from 'semantic-ui-react'

import FooterController from './FooterController'
import FrontPage from '../presentationcomponents/FrontPage'


export default class FrontPageController extends Component {
    render() {
        return (
            <Grid>
                <Grid.Column width={16} className="main-body-wrapper">
                    <FrontPage />
                </Grid.Column>
                <Grid.Column width={16}>
                    <FooterController />
                </Grid.Column>
            </Grid>
        )
    }
}