import { Grid } from 'semantic-ui-react'

import avatar from '../../images/avatar.svg'


export default function FrontPage() {
    return (
        <Grid className="main-body-section-layout">
            <Grid.Column width={2} />
            <Grid.Column width={14}>
                <Grid>
                    <Grid.Column width={10}>
                        <h1 className="header-text hero-main-text homepage-hero-gradient-wrap">
                            <div className="homepage-hero-gradient"></div>
                            Hi! I&apos;m Sawan Ashok
                            <br />Fullstack Developer
                        </h1>
                        <p className="subheader-text subheader-padding" style={{marginTop: '30px'}}>
                            A Product driven - Fullstack, Frontend engineer with 2+ years of experience in creating consumer first product.
                        </p>
                        <div style={{marginTop: '30px'}} className="subheader-padding hero-contact-info-wrapper">
                            <div className="hero-contact-info-components">
                                <span className="subheader-text"><b>Email:</b></span>
                                <span className="subheader-text"
                                    style={{marginTop: '10px'}}>sawan.ashok666@gmail.com</span>
                            </div>
                            <div className="hero-contact-info-components">
                                <span className="subheader-text"><b>Phone No:</b></span>
                                <span className="subheader-text"
                                    style={{marginTop: '10px'}}>+91-28982992222</span>
                            </div>
                        </div>
                    </Grid.Column>
                    
                    <Grid.Column width={6}>
                        <img src={avatar} />
                    </Grid.Column>
                </Grid>
            </Grid.Column>
        </Grid>
    )
}