import { Grid, Icon } from 'semantic-ui-react'


export default function Footer() {
    return (
        <Grid className='footer-wrapper'>
            <div className='footer-mask'>
                <div className='footer-background'></div>
            </div>
            <Grid.Column width={3} />
            <Grid.Column width={10} className="footer-main-body">
                <h1 className="header-text white-text" style={{textAlign: 'center'}}>
                    Let&apos;s Talk!</h1>
                <div style={{marginTop: '50px'}} 
                    className='hero-contact-info-wrapper footer-components-wrapper'>
                    <div className="hero-contact-info-components footer-components">
                        <span className="subheader-text white-text">
                            <Icon name="phone" /></span>
                        <span className="subheader-text white-text"
                            style={{marginTop: '10px'}}><b>Phone Me:</b></span>
                        <span className="subheader-text white-text"
                            style={{marginTop: '10px'}}>+91-28982992222</span>
                    </div>
                    <div className="hero-contact-info-components footer-components">
                        <span className="subheader-text white-text">
                            <Icon name="send" /></span>
                        <span className="subheader-text white-text"
                            style={{marginTop: '10px'}}><b>Mail Me:</b></span>
                        <span className="subheader-text white-text"
                            style={{marginTop: '10px'}}>sawan.ashok666@gmail.com</span>
                    </div>
                    <div className="hero-contact-info-components footer-components">
                        <span className="subheader-text white-text">
                            <Icon name="github" /></span>
                        <span className="subheader-text white-text"
                            style={{marginTop: '10px'}}><b>Github Link:</b></span>
                        <span className="subheader-text white-text"
                            style={{marginTop: '10px'}}>http://localhost:3000/home</span>
                    </div>
                </div>
            </Grid.Column>
        </Grid>
    )
}